    background-image: url("data:image/svg+xml,%3Csvg id='calendar-line' xmlns='http://www.w3.org/2000/svg' width='15.043' height='15.043' viewBox='0 0 15.043 15.043'%3E%3Cpath id='Path_506' data-name='Path 506' d='M0,0H15.043V15.043H0Z' fill='none'/%3E%3Cpath id='Path_507' data-name='Path 507' d='M11.4,2.254h2.507a.627.627,0,0,1,.627.627V12.909a.627.627,0,0,1-.627.627H2.627A.627.627,0,0,1,2,12.909V2.88a.627.627,0,0,1,.627-.627H5.134V1H6.387V2.254h3.761V1H11.4ZM10.148,3.507H6.387V4.761H5.134V3.507H3.254V6.014H13.282V3.507H11.4V4.761H10.148Zm3.134,3.761H3.254v5.014H13.282Z' transform='translate(-0.746 -0.373)' fill='%23666'/%3E%3C/svg%3E%0A")!important;
    padding-right: 2.2rem;
    background-repeat: no-repeat;
    background-position: right 0.65rem center;
    background-size: 16px;
}
.custom-file-label {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.custom-switch-lg {
    .custom-control-label {
        &::before {
            left: -2.25rem;
            width: 3.75rem;
            border-radius: 1rem;
            height: 1.75rem;
        }

        &::after {
            top: calc(.35rem + 2px);
            left: calc(-2rem + 2px);
            width: calc(1.5rem - 4px);
            height: calc(1.5rem - 4px);
            border-radius: 1rem;
        }
    }

    .custom-control-input:checked ~ .custom-control-label::after {
        transform: translateX(1.75rem);
    }
}

