//Custom theme variable overridden for Bootstrap
@import "components/variables";

//Configured all Bootstrap and theme scss files are located here.
@import "config";

.swiper-container {
    padding-bottom: 3.5rem!important;
}
.swiper-pagination-bullet-active {
    background: $primary!important;
}

// Media Manager

.fileuploader {
    display: flex;
    &--theme-avatar {
        flex-direction: column;
        align-items: center;
        padding: 3rem;
        &:hover {
            .fileuploader__action {
                transform: scale(1);
            }
        }
    }
    &__wrapper {
        display: flex;
        width: 90px;
        height: 90px;
        position: relative;
        margin-bottom: 1rem;
    }
    &__list {
        flex: 1;
    }
    &__items {
        li {
            list-style: none;
        }
    }
    &__item {
        //display: inline-flex;
        position: relative;
    }
    &__item-image {
        display: flex;
        position: relative;
        overflow: hidden;
        border-radius: 50%;
        height: 90px;
        justify-content: center;
        align-items: center;
        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: cover;
        }
    }
    &__action {
        color: var(--danger);
        padding: 0;
        margin: 0;
        line-height: 1;
        transform: scale(0);
        transition: $transition-base;
        border: 1px solid white;
        border-radius: 50%;
        background: white;
    }
    &__action-remove {
        color: var(--danger);
        padding: 0;
        line-height: 1;
    }
    &__icon-remove {
        font-size: 1.28rem; 
    }
}
.actions-holder {
    position: absolute;
    top: 0.2rem;
    right: 0.2rem;
}

// Media Manager Iframe
#media .modal-body {
    overflow: hidden;
}
#media .modal-body iframe {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
}
#media .iframe-container {
    padding-bottom: calc(100% - 4rem);
}

